class Home extends Component {
    render() {
      return (
        <div>
          <ConfessSection/>
        </div>
      )
    }
}

//FRONTEND SELECT POST BACKGROUND
function selectPostBackground(backgroundId, This)  {
  This.setState({
    postBackgroundActive: backgroundId
  })
}

//FRONTEND CONFESS UPLOAD SUCCESSFUL
function ConfessUploadSuccessful(This) {
  //EDIT FORM HEADER & HIDE ALL COMPONENTS INSIDE FORM
  This.setState({
    ConfessUploadMsg: 'CONFESS CỦA BẠN ĐÃ GỬI THÀNH CÔNG!',
    ConfessUploadSuccessful: 'none'
  })
}

//FUNCTION UPLOAD CONFESS
function ConfessUpload(This) {
  let confess = This.refs.confessInput.value;
  let hashtag = This.refs.hashtagInput.value;
  let image = This.state.imageName;
  let background = This.state.postBackgroundActive;
  let date = new Date();
  var thiss = This;

  if(confess === "") {
    thiss.setState({
      msg : "Hãy nhập confess đi :3"
    })
  }else {
    //GET USER DATA
    Axios.get("https://www.cloudflare.com/cdn-cgi/trace").then((result) => {
    let data = {
      noidung: confess,
      hashtag: hashtag,
      image: [image],
      ngayTao: date,
      user: 'User',
      video: null,
      clientData: '',
      background: background,
      clientData: result.data,
      analytics: window.location.search,
      confess_id: ""
    }
    
    ConfessUploadSuccessful(thiss);

    //POST TO SERVER
    Axios.post(`${SERVER_LINK}/api/v1/send_confess`, data).then((res) => {
    });
    })
  }
}

//FUNCTION UPLOAD IMAGEIMAGE
function ImageUpload(File, This) {
  let formData = new FormData();

  formData.append("image", File);
  let config = {
    headers: {
        'content-type': 'multipart/form-data'
    }
  }

  Axios.post(`${SERVER_LINK}/uploadImage`, formData, config).then(res => {
    This.setState({
      image: `url(${SERVER_LINK}/media/` + res.data.fileName +') no-repeat center center / cover',
      imageName: res.data.fileName,
      hasImage: 'flex'
    })
  });
}

export default Home;