import Axios from 'axios';
import {SERVER_LINK} from '../components/Global';

function getAllConfess(callback) {
    Axios.get(`${SERVER_LINK}/api/v1/get_all_confess`).then((data) => {
      return callback(data.data);
    });
}

function getSingleConfess(post_id, callback) {
    Axios.get(`${SERVER_LINK}/api/v1/get_single_confess/${post_id}`).then((data)=> {
      return callback(data.data)
    })
  }

export {
    getAllConfess,
    getSingleConfess
}