import React, {Component} from 'react';
import {ConfessSinglePost} from '../components/Confess_Post';
import {CommentsContainer} from '../components/Comments_Container';
import {Header} from '../components/Header/Header';
import {SERVER_LINK} from '../components/Global';
import io from 'socket.io-client';

const socket = io(SERVER_LINK);

class PostPreview extends Component {
    constructor(props) {
        super(props);
        this.state = {
            post_id: this.props.match.params.post_id
        }
    }

    render() {
        return(
            <div className="postPreviewView">
                <Header title="POST" HistoryParent={this}/>
                <ConfessSinglePost post_id={this.state.post_id}/>
                <CommentsContainer socket={socket} post_id={this.state.post_id}/>
            </div>
        )
    }
}

export default PostPreview;